@import "./variable";
@import "./mixin";

body {
  display: none;
  position: relative;
  margin: 0;
  font-family: $font-family;
}

*, *:before, *:after {
  box-sizing: border-box;
}

a {
  transition: all .3s;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.custom_container {
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 20px;
}

h2 {
  font-weight: 500;
  font-size: 44px;
  line-height: 1.25;
  color: #4D4D4D;
}

h3 {
  font-weight: 500;
  font-size: 32px;
  line-height: 1.25;
  color: #4D4D4D;
}

h4 {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.4;
  color: #999999;
}

h5 {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.4;
  text-align: center;
  color: #999999;
}

@import "./components/header";
@import "./components/components";
@import "./pages/index";
@import "./pages/upload";
@import "./pages/sign";
@import "./pages/finish";