header {
  position: fixed;
  width: 100%;
  height: 80px;
  z-index: 99;
  background-color: #000000;
}

.header_container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.logo {
  display: block;
  padding: 18px 0;
  > img{
    width: 228px;
  }
}

.header_nav_container {
  display: flex;
  align-items: center;
}

.help {
  display: block;
  padding: 20px 6px;
  transition: 0.3s all;
  > span {
    display: block;
    width: 40px;
    height: 40px;
    background-image: url(#{$imgs}icons/ic_help.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &:hover {
    > span {
      background-image: url(#{$imgs}icons/ic_help_h.svg);
    }
  }
}

.login {
  display: block;
  padding: 15px 0 15px 6px;
  transition: 0.3s all;
  > span {
    display: block;
    width: 50px;
    height: 50px;
    background-image: url(#{$imgs}icons/ic_user.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &:hover {
    > span {
      background-image: url(#{$imgs}icons/ic_user_h.svg);
    }
  }
}

.timeline_container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 474px;
  margin: 0 auto;
  padding: 18px 0;
  .step {
    margin: 0 auto;
    margin-bottom: 8px;
    width: 16px;
    height: 16px;
    background-color:#000000;
    border: 2px solid #999999;
    border-radius: 50%;
    &:after {
      display: block;
      content: '';
      width: 194px;
      height: 2px;
      background-color: #999999;
      position: relative;
      top: 50%;
      left: 14px;
      transform: translateY(-50%);
    }
    &.active {
      &:after {
        background-color: #B7EC5D;
      }
    }
  }
  .box {
    &:last-child {
      .step {
        &:after {
          display: none;
        }
      }
    }
    &.active {
      .step {
        position: relative;
        border-color: #B7EC5D;
        &:before {
          display: block;
          content: '';
          width: 24px;
          height: 24px;
          border: 6px solid rgba(#B7EC5D, 0.6);
          border-radius: 50%;
          position: absolute;
          top: -6px;
          left: -6px;
        }
      }
      h5 {
        color: #B7EC5D;
      }
    }
    &.complete {
      .step {
        border-color: #B7EC5D;
        background-color: #B7EC5D;
      }
    }
  }
  @include rwd-mobile(md) {
    top: 74px;
    transform: translateX(-50%);
    width: 435px;
    .box {
      h5 {
        display: none;
      }
    }
  }
}