.sign_block_container {
  .title_block {
    margin-bottom: 0;
  }
}

.sign_container {
  display: flex;
  align-items: flex-start;
}

.tool_block {
  width: 30%;
  height: calc(100vh - 300px);
  border-right: 2px solid #B7EC5D;
}

.edit_block {
  width: 70%;
  height: calc(100vh - 300px);
  .upload_preview_box {
    height: 100%;
    overflow: auto;
    padding: 40px 0;
    text-align: center;
    background: #B3B3B3;
    .canvas-container {
      margin: 0 auto;
    }
  }
}

.tool_box {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  margin-bottom: 32px;
  .ico_box {
    margin: 0 12px;
    transition: 0.3s all;
    > span {
      display: block;
      width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      padding-top: 44px;
      font-weight: 500;
      font-size: 14px;
      line-height: 1.4;
      text-align: center;
      color: #4D4D4D;
    }
    &.ico_sign {
      background-image: url(#{$imgs}icons/ic_sign.svg);
    }
    &.ico_pic {
      background-image: url(#{$imgs}icons/ic_pic.svg);
    }
    &.ico_text {
      background-image: url(#{$imgs}icons/ic_text.svg);
    }
    &.ico_page {
      background-image: url(#{$imgs}icons/ic_page.svg);
    }
    &:hover, &.active {
      &.ico_sign {
        background-image: url(#{$imgs}icons/ic_sign_h.svg);
      }
      &.ico_pic {
        background-image: url(#{$imgs}icons/ic_pic_h.svg);
      }
      &.ico_text {
        background-image: url(#{$imgs}icons/ic_text_h.svg);
      }
      &.ico_page {
        background-image: url(#{$imgs}icons/ic_page_h.svg);
      }
    }
  }
}

.sign_box {
  margin: 0 auto;
  background: #F5F5F5;
  border-radius: 20px;
  width: calc(100% - 20px);
  height: calc(100vh - 396px);
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  .sign_card_box {
    max-height: calc(100% - 80px);
    min-height: 300px;
    margin-bottom: 20px;
    > div {
      max-height: 100%;
      overflow: auto;
      margin: 0;
    }
    .sign_card {
      position: relative;
      width: 100%;
      height: 19%;
      background: #FFFFFF;
      border-radius: 20px;
      margin-bottom: 8px;
      cursor: pointer;
      .ico_box {
        position: absolute;
        top: 0;
        left: 0;
        display: none;
        transition: 0.3s all;
        > span {
          display: block;
          width: 40px;
          height: 40px;
          background-image: url(#{$imgs}icons/ic_close_s.svg);
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        &:hover {
          > span {
            background-image: url(#{$imgs}icons/ic_close_s_white.svg);
          }
        }
      }
      &:hover {
        background: rgba(183, 236, 93, 0.7);
        border: 2px solid #B7EC5D;
        .ico_box {
          display: block;
        }
      }
    }
  }
  .sign_add_box {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .btn_add_file {
      margin-bottom: 0;
      > span {
        width: 60px;
        height: 60px;
      }
    }
    h2 {
      display: none;
    }
  }
  &.no_sign {
    justify-content: center;
    .sign_add_box {
      .btn_add_file {
        margin-bottom: 16px;
        > span {
          width: 80px;
          height: 80px;
        }
      }
      h2 {
        display: block;
        font-size: 24px;
        line-height: 1.4;
      }
    }
  }
}