.btn {
  background: #FFFFFF;
  border: 2px solid #808080;
  border-radius: 50px;
  color: #808080;
  padding: 8px 28px;
  transition: 0.3s all;
  &:hover {
    border: 2px solid #B7EC5D;
    color: #B7EC5D;
  }
  &.disabled {
    background: #CCCCCC;
    border: 2px solid #B3B3B3;
    color: #808080;
  }
}

.btn_dark {
  background: #4D4D4D;
  border: 2px solid #B7EC5D;
  color: #B7EC5D;
  border-radius: 50px;
  padding: 8px 28px;
  transition: 0.3s all;
  &:hover {
    background: #B7EC5D;
    color: #FFFFFF;
  }
}

.btn_add {
  display: none;
  position: absolute;
  top: 96px;
  right: -48px;
  z-index: 1;
  transition: 0.3s all;
  > span {
    display: block;
    width: 80px;
    height: 80px;
    background-image: url(#{$imgs}icons/ic_add_tint.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &:hover {
    > span {
      background-image: url(#{$imgs}icons/ic_add_dark.svg);
    }
  }
}

.btn_add_file {
  transition: 0.3s all;
  margin-bottom: 28px;
  > span {
    display: block;
    width: 136px;
    height: 136px;
    background-image: url(#{$imgs}icons/ic_add_dark.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &:hover {
    > span {
      background-image: url(#{$imgs}icons/ic_add_tint.svg);
    }
  }
}

.ico_box {
  margin-left: 8px;
  transition: 0.3s all;
  > span {
    display: block;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  &.ico_download {
    background-image: url(#{$imgs}icons/ic_download.svg);
  }
  &.ico_archive {
    background-image: url(#{$imgs}icons/ic_archive.svg);
  }
  &.ico_trash {
    background-image: url(#{$imgs}icons/ic_trash.svg);
  }
  &.ico_reduction {
    background-image: url(#{$imgs}icons/ic_reduction.svg);
  }
  &:hover {
    &.ico_download {
      background-image: url(#{$imgs}icons/ic_download_h.svg);
    }
    &.ico_archive {
      background-image: url(#{$imgs}icons/ic_archive_h.svg);
    }
    &.ico_trash {
      background-image: url(#{$imgs}icons/ic_trash_h.svg);
    }
    &.ico_reduction {
      background-image: url(#{$imgs}icons/ic_reduction_h.svg);
    }
  }
}

.toast {
  position: absolute;
  top: 96px;
  left: 50%;
  transform: translateX(-50%);
  width: 416px;
  background: rgba(100, 141, 30, 0.9);
  border: 2px solid #B7EC5D;
  border-radius: 50px;
  .toast-body {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.4;
    color: #FFFFFF;
    padding-left: 24px;
  }
  &.error {
    background: rgba(249, 56, 25, 0.9);
  }
  .btn-close {
    width: 40px;
    height: 40px;
    background-image: url(#{$imgs}icons/ic_close_s_white.svg);
    background-size: contain;
    padding: 0;
    opacity: 1;
    &:focus-visible {
      outline: none;
    }
    &:focus {
      box-shadow: none;
    }
  }
}

.modal {
  .modal-content {
    padding: 24px 32px;
  }
  .modal-header {
    padding: 0;
    border-bottom: 2px solid #B7EC5D;
    .modal-title {
      font-weight: 500;
      font-size: 24px;
      line-height: 1.4;
      color: #4D4D4D;
      margin: 0 auto;
      padding-bottom: 16px;
    }
  }
  .modal-body {
    padding: 72px 0;
    p {
      margin: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 1.6;
      text-align: center;
      color: #4D4D4D;
    }
  }
  .modal-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: none;
    padding: 0;
    > button {
      width: 205px;
    }
  }
}

.sign_modal {
  .modal-dialog {
    max-width: 724px;
  }
  .modal-body {
    padding: 40px 0;
  }
  ul.tool_list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 12px;
    > li {
      .ico_box {
        display: block;
        margin: 0 6px;
        transition: 0.3s all;
        > span {
          display: block;
          width: 40px;
          height: 40px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        &.ico_color_black {
          background-image: url(#{$imgs}icons/ic_color_black.svg);
        }
        &.ico_color_blue {
          background-image: url(#{$imgs}icons/ic_color_blue.svg);
        }
        &.ico_color_red {
          background-image: url(#{$imgs}icons/ic_color_red.svg);
        }
        &.ico_trash {
          background-image: url(#{$imgs}icons/ic_trash.svg);
        }
        &:hover, &.active {
          &.ico_color_black {
            background-image: url(#{$imgs}icons/ic_color_black_h.svg);
          }
          &.ico_color_blue {
            background-image: url(#{$imgs}icons/ic_color_blue_h.svg);
          }
          &.ico_color_red {
            background-image: url(#{$imgs}icons/ic_color_red_h.svg);
          }
          &.ico_trash {
            background-image: url(#{$imgs}icons/ic_trash_h.svg);
          }
        }
      }
    }
  }
  .sign_canvas {
    background: #F5F5F5;
    border: 2px solid #B3B3B3;
    border-radius: 20px;
  }
}