.content_sec {
  height: 100vh;
  padding-top: 80px;
  background-color: #333333;
  background-image: url(#{$imgs}bg_decorate.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.content_container {
  position: relative;
  padding: 40px 0;
  max-width: 1420px;
  margin: 0 auto;
  .tab_list_container {
    position: absolute;
    top: 141px;
    left: -76px;
  }
  .tab_button {
    background: #666666;
    border: 2px solid #4D4D4D;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
    border-radius: 100px 0px 0px 100px;
    margin: 5px 0;
    padding: 24px 12px 24px 24px;
    &:hover, &.active {
      background: #FFFFFF;
      border: 2px solid #B7EC5D;
    }
  }
  .tab_content {
    background: #FFFFFF;
    box-shadow: inset 0px 2px 4px rgba(135, 135, 135, 0.35), inset 0px 5px 8px rgba(215, 215, 215, 0.3), inset 0px -2px 4px rgba(135, 135, 135, 0.65), inset 0px -6px 6px rgba(215, 215, 215, 0.55);
    border-radius: 40px;
    padding: 40px 32px;
    width: 100%;
    height: calc(100vh - 160px);
    z-index: 1;
    min-height: 600px;
    .tab-pane {
      height: calc(100% - 80px);
    }
  }
  .content_tool_box {
    display: flex;
    align-items: center;
    justify-content: center;
    .list_box.active {
      background: linear-gradient(180deg, rgba(183, 236, 93, 0) 0%, rgba(183, 236, 93, 0.1) 86.46%, rgba(183, 236, 93, 0.3) 100%);
      border: 4px solid rgba(183, 236, 93, 0.3);
      .file_time {
        .ico_file {
          > span {
            background-image: url(#{$imgs}icons/ic_file_item_h.svg);
          }
        }
      }
    }
  }
}

.title_block {
  padding-left: 16px;
  padding-bottom: 16px;
  border-bottom: 2px solid #B7EC5D;
}

.list_block {
  padding: 22px 16px;
  height: 100%;
  .list_title_block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 68px;
    .list_title {
      margin: 0;
      font-weight: 500;
      font-size: 14px;
      line-height: 1.40;
      letter-spacing: 0.05em;
      padding-top: 20px;
      padding-bottom: 16px;
      text-align: left;
      &:nth-child(1) {
        width: 242px;
      }
      &:nth-child(2) {
        flex: 1 1 auto;
        padding-left: 12px;
      }
    }
    .tool_box {
      display: flex;
      align-items: center;
      .ico_list {
        transition: 0.3s all;
        > span {
          display: block;
          width: 40px;
          height: 40px;
          background-image: url(#{$imgs}icons/ic_list.svg);
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        &:hover, &.active {
          > span {
            background-image: url(#{$imgs}icons/ic_list_h.svg);
          }
        }
      }
      .ico_card {
        transition: 0.3s all;
        > span {
          display: block;
          width: 40px;
          height: 40px;
          background-image: url(#{$imgs}icons/ic_card.svg);
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        &:hover, &.active {
          > span {
            background-image: url(#{$imgs}icons/ic_card_h.svg);
          }
        }
      }
    }
  }
}

.list_content_block {
  max-height: calc(100vh - 320px);
  overflow: auto;
  .list_box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 24px 16px 16px;
    background: #FFFFFF;
    border: 1px solid #F5F5F5;
    box-shadow: 0px 1px 5px rgba(179, 179, 179, 0.4), 0px 2px 8px rgba(215, 215, 215, 0.4), inset 0px 2px 5px rgba(215, 215, 215, 0.16), inset 0px -2px 3px rgba(179, 179, 179, 0.6), inset 0px -6px 6px rgba(215, 215, 215, 0.2);
    border-radius: 20px;
    margin: 8px 0;
    &:hover {
      background: linear-gradient(180deg, rgba(183, 236, 93, 0) 0%, rgba(183, 236, 93, 0.1) 86.46%, rgba(183, 236, 93, 0.3) 100%);
      border: 4px solid rgba(183, 236, 93, 0.3);
      cursor: pointer;
    }
  }
  .file_time {
    width: 292px;
    display: flex;
    align-items: center;
    .ico_file {
      margin-right: 12px;
      > span {
        display: block;
        width: 40px;
        height: 40px;
        background-image: url(#{$imgs}icons/ic_file_item.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
  .file_name {
    padding-left: 12px;
    text-align: left;
    flex: 1 1 auto;
  }
}

.card_content_block {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 -10px;
  max-height: calc(100vh - 370px);
  overflow: auto;
  .card_container {
    flex: 0 0 25%;
    text-align: center;
    margin: 8px 0;
    .gap {
      padding: 0 10px;
      > .card_box {
        display: block;
        margin: 0;
        background: #FFFFFF;
        border: 1px solid #F5F5F5;
        box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.12), 0px 2px 10px rgba(0, 0, 0, 0.08), inset 0px -2px 4px rgba(215, 215, 215, 0.5);
        border-radius: 20px;
        padding: 16px;
      }
    }
    .file_img {
      margin: 0;
      > img {
        width: 92px;
        height: 130px;
      }
    }
    .file_name {
      font-weight: 500;
      font-size: 16px;
      line-height: 1.6;
      letter-spacing: 0.05em;
      color: #4D4D4D;
      margin-bottom: 4px;
    }
    .file_time {
      font-weight: 500;
      font-size: 14px;
      line-height: 1.4;
      letter-spacing: 0.05em;
      color: #999999;
      margin-bottom: 16px;
    }
    &:hover {
      .gap {
        > .card_box {
          background: linear-gradient(180deg, rgba(183, 236, 93, 0) 0%, rgba(183, 236, 93, 0.1) 86.46%, rgba(183, 236, 93, 0.3) 100%);
          border: 4px solid rgba(183, 236, 93, 0.3);
          cursor: pointer;
        }
      }
    }
  }
}

.no_data_block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  p {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.6;
    color: #999999;
    margin-top: 12px;
  }
  img {
    margin-bottom: 30px;
  }
}