.block_container {
  background: #FFFFFF;
  box-shadow: inset 0px 2px 4px rgba(135, 135, 135, 0.35), inset 0px 5px 8px rgba(215, 215, 215, 0.3), inset 0px -2px 4px rgba(135, 135, 135, 0.65), inset 0px -6px 6px rgba(215, 215, 215, 0.55);
  border-radius: 40px;
  padding: 40px 32px;
  width: 100%;
  height: calc(100vh - 160px);
  z-index: 1;
  min-height: 600px;
  .title_block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 60px;
  }
}

.upload_block {
  height: calc(100% - 146px);
  margin: 0 28px;
  border: 1px dashed #4D4D4D;
  border-radius: 20px;
  transition: 0.3s all;
  &.active {
    border-width: 3px;
  }
  .upload_box {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    h4 {
      margin-top: 32px;
      margin-bottom: 12px;
    }
    p {
      margin: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 1.6;
      text-align: center;
      color: #999999;
    }
  }
  input {
    display: none;
  }
  img {
    margin-bottom: 20px;
  }
}

.upload_edit_block {
  height: calc(100% - 146px);
  display: none;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  .file_img_box {
    position: relative;
    .btn_delete {
      position: absolute;
      top: -30px;
      right: -30px;
      display: block;
      width: 40px;
      height: 40px;
      background-image: url(#{$imgs}icons/ic_close.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: 0.3s all;
      cursor: pointer;
      &:hover {
        background-image: url(#{$imgs}icons/ic_close_h.svg);
      }
    }
  }
  .file_info_box {
    margin-top: 23px;
    margin-bottom: 40px;
    .file_name {
      font-weight: 500;
      font-size: 24px;
      line-height: 1.4;
      color: #4D4D4D;
      text-align: center;
      margin-bottom: 4px;
    }
    .file_page {
      font-weight: 500;
      font-size: 16px;
      line-height: 1.6;
      color: #999999;
      text-align: center;
      margin: 0;
    }
  }
  .project_box {
    .project_name {
      font-weight: 500;
      font-size: 16px;
      line-height: 1.6;
      color: #4D4D4D;
      text-align: center;
      margin-bottom: 8px;
    }
  }
  .project_input {
    position: relative;
    > input {
      border: 2px solid #4D4D4D;
      border-radius: 50px;
      width: 400px;
      padding: 8px 24px;
      &:focus-visible {
        border: 2px solid #B7EC5D;
        outline: none;
      }
    }
    > span {
      display: block;
      width: 40px;
      height: 40px;
      background-image: url(#{$imgs}icons/ic_edit.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
    }
  }
}